<template>
  <div class="custom-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">防护管理</el-breadcrumb-item>
        <el-breadcrumb-item>基础组件</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>

    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-row class="text-align-right">
          <div class="data-search-input">
            <el-input placeholder="请输入名单名称进行搜索" prefix-icon="el-icon-search" v-model="dataSearch">
            </el-input>
            <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
          </div>
          <el-button type="success" @click="onClickChangeOrder()">优先级调整</el-button>
          <el-button type="primary" @click="onClickCreate()">新建组件</el-button>
        </el-row>
        <div class="demo-block">
          <el-table
            :data="tableData.filter((data) => !dataSearch || data.name.toLowerCase().includes(dataSearch.toLowerCase())) "
            style="width: 100%">
            <el-table-column prop="name" label="组件名称"></el-table-column>
            <el-table-column prop="detail" label="组件描述"></el-table-column>
            <el-table-column prop="conf" label="配置"></el-table-column>
            <el-table-column prop="status" label="状态">
              <template #default="scope">
                <el-switch v-model="scope.row.status" @change="onChangeRuleStatus(scope.row)" active-value="true"
                  inactive-value="false" />
              </template>
            </el-table-column>

            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text">编辑</el-button>
                <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                  <p>确定删除吗？</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                    </el-button>
                  </div>
                  <template #reference>
                    <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                  </template>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="优先级" align="right" v-if="!isShowOrder">
              <template #default="scope">
                <el-button type="success" class="icon iconfont iconxiangshang" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'up')" title="上移" :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconxiangxia" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'down')" title="下移"
                  :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconzhiding" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'top')" title="置顶"
                  :loading="orderLoading" />
              </template>
            </el-table-column>

          </el-table>
        </div>
      </el-col>

      <el-dialog v-model="dialogCreateFormVisible" :title="title" :close-on-click-modal="false" width="520px"
        @closed="dialogCloseCreate">
        <el-form class="form-download-rule-dialog" :model="createForm" label-position="left" label-width="130px"
          :rules="rules" ref="createForm">
          <el-form-item label="组件名称" key="1" prop="name">
            <el-input v-model="createForm.name" placeholder="Please input" v-if="type == 'new'" />
            <el-input v-model="createForm.name" placeholder="Please input" disabled v-else />
          </el-form-item>
          <el-form-item label="组件描述" key="2">
            <el-input v-model="createForm.detail" placeholder="Please input" />
          </el-form-item>
          <el-form-item label="CODE" key="3" prop="code">
            <el-input v-model="createForm.code" :rows="5" type="textarea" placeholder="Please input" />
          </el-form-item>
          <el-form-item label="默认配置" key="4" class="is-required">
            <el-input v-model="create_conf" :rows="5" type="textarea" placeholder="Please input" />
          </el-form-item>
        </el-form>

        <template #footer class="dialog-footer">
          <el-button @click="dialogCreateFormVisible = false">取消</el-button>
          <el-button type="primary" @click="onClickCreateSubmit('createForm')" :loading="loading">确定
          </el-button>
        </template>
      </el-dialog>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      isShowOrder: true,
      orderLoading: false,
      tableData: [],
      createForm: {
        detail: ""
      },
      conf: {},
      create_conf: "",
      title: "新建组件",
      type: "new",
      dialogCreateFormVisible: false,
      dataSearch:"",
    };
  },
  computed: {
    rules() {
      return {
        name: [
          { required: true, message: '请输入组件名称', trigger: "blur" },
        ],

        code: [
          { required: true, message: '请输入code', trigger: "blur" },
        ],
        
      }
    }
  },
  mounted() {
    this.getData();
    
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_base_component_list";
      var oData = {  };
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },

    handleDelete(data) {
      var t = this;
      t.loading = true;
      var postUrl = "/waf/waf_del_base_component";
      var oData = {
        name: data.name
      };
      
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
    onClickChangeOrder() { 
      var t = this;
      t.isShowOrder = !t.isShowOrder;
    },
    onClickChangeOrderSubmit(index, value, operate) { 
      var t = this;
      
      var oData = {
        name: value.name,
      };
      if (index > 0) { 
        if (operate == "top") {
          oData.type = "top";
        }
        if (operate == "up") {
          oData.type = "exchange";
          oData.exchange_name = t.tableData[index - 1].name;
        }
      }

      if (index < t.tableData.length - 1) { 
        if (operate == "down") {
          oData.type = "exchange";
          oData.exchange_name = t.tableData[index + 1].name;
        }
      }
      var postUrl = "/waf/waf_exchange_base_component_priority";

      
      if (oData.type == "top" || oData.type == "exchange") { 
        t.orderLoading = true;  
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.orderLoading = false;
            t.getData();
          },
          function () {
            t.orderLoading = false;
          },
          "no-message"
        );
      }
      
    },
    onChangeRuleStatus(value) { 
      var t = this;
      var oData = {
        name: value.name,
        status: value.status
      }
      var postUrl = "/waf/waf_edit_base_component_status";
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.getData();
        },
        function () {
          
        },
        "no-message"
      );
    },
    handleEdit(data) {
      this.dialogCreateFormVisible = true;
      this.type = "edit";
      this.title = "编辑组件";
      var t = this;
      t.loadingPage = true;
      var postUrl = "/waf/waf_get_base_component";
      var oData = { name :data.name};
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.createForm = response.data.message;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
      var jsonObj = JSON.parse(data.conf)
      this.create_conf = JSON.stringify(jsonObj, null, 4);
    },
    onClickCreate() {
      this.dialogCreateFormVisible = true;
      this.type = "new";
      this.title = "新建组件";
      this.create_conf = "";
    },
    dialogCloseCreate() {
      this.createForm = {
        detail: ""
      };
      this.create_conf = "";
      this.$refs["createForm"].resetFields();
    },
    onClickCreateSubmit(createForm) {
      var t = this;
      var o = "";
      var jsonObj = {};
      var postUrl = "/waf/waf_edit_base_component";
      if (t.type == "new") {
        postUrl = "/waf/waf_create_base_component";
      }
      try {
        o = JSON.stringify(JSON.parse(t.create_conf));
        jsonObj = JSON.parse(t.create_conf);
        t.create_conf = JSON.stringify(jsonObj, null, 4);
      } catch (error) {
        t.$message({
          showClose: true,
          message: '请输入正确JSON格式',
          type: "error",
        });
        return false;
      }

      t.createForm.conf = o;
      this.$refs[createForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.createForm,
            function (response) {
              t.loading = false;
              t.dialogCreateFormVisible = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    onChangeSearch() {
      var t = this;
      t.loadingPage = true;
      setTimeout(function () {
        t.loadingPage = false;
      }, 300)

    },
  },
};
</script>
<style>


.custom-wrap {
  /* max-width: 800px;
  min-width: 400px; */
}
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}

.custom-wrap .rule-matchs-content .match-box-title {
  display: inline-block;
  text-align: left;
  font-size: 14px;
  color: #909399;
  padding: 0 5px;
  box-sizing: border-box;
}
.custom-wrap .rule-matchs-content .match-box-title:before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
.custom-wrap .match-inline-block {
  width: 192px;
}
.custom-wrap .match-inline-block-small {
  width: 150px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
</style>
